const getters = {
  isOpenCoordinate: state =>state.newdev.isOpenCoordinate,
  canSelPoint: state =>state.newdev.canSelPoint,
  searchPointCoordArr: state =>state.newdev.searchPointCoordArr,
  markLngLat: state =>state.newdev.markLngLat,
  username: state =>state.newdev.username,
  tablename: state =>state.newdev.tablename,
  needRefreshTarget: state =>state.newdev.needRefreshTarget,
};
export default getters



