export const ISOPENCOORDINATE = 'ISOPENCOORDINATE';//搜索设置打开坐标
export const CANSELPOINT = 'CANSELPOINT';//开启地图选点
export const SEARCHPOINTCOORDARR = 'SEARCHPOINTCOORDARR';//地图选点的坐标
export const MARKLNGLAT = 'MARKLNGLAT';//地图marker的坐标

export const USERNAME = 'USERNAME';//用户名
export const TABLENAME = 'TABLENAME';//用户选择的数据源（地址编辑）
export const NEEDREFRESHTARGET = 'NEEDREFRESHTARGET';//用户选择的数据源（地址编辑）

const newdev = {
  state: {
    isOpenCoordinate: false,   //是否开启坐标查询
    canSelPoint: false,   //是否开启坐标查询
    searchPointCoordArr: [],   //地图选点的坐标
    markLngLat:undefined,//地图marker坐标

    username: 'user_admin',   //用户名
    tablename: null,   //数据源表名
    needRefreshTarget: '1',   //需要刷新数据源列表
  },
  mutations: {
    [ISOPENCOORDINATE](state, val) {
      state.isOpenCoordinate = val;
    },
    [CANSELPOINT](state, val) {
      state.canSelPoint = val;
    },
    [SEARCHPOINTCOORDARR](state, val) {
      state.searchPointCoordArr = val;
    },
    [MARKLNGLAT](state, val) {
      state.markLngLat = val;
    },
    [USERNAME](state, val) {
      state.username = val;
    },
    [TABLENAME](state, val) {
      state.tablename = val;
    },
    [NEEDREFRESHTARGET](state, val) {
      state.needRefreshTarget = val;
    },
  },
  actions: {
    updateIsOpenCoordinate({ commit }, val) {
      commit(ISOPENCOORDINATE, val);
    },
    updateCanSelPoint({ commit }, val) {
      commit(CANSELPOINT, val);
    },
    updateSearchPointCoordArr({ commit }, val) {
      commit(SEARCHPOINTCOORDARR, val);
    },
    updateMarkLngLat({ commit }, val) {
      commit(MARKLNGLAT, val);
    },
    updateUsername({ commit }, val) {
      commit(USERNAME, val);
    },
    updateTablename({commit}, val) {
      commit(TABLENAME, val);
    },
    updateNeedRefreshTarget({commit}, val) {
      commit(NEEDREFRESHTARGET, val);
    },
  }
};

export default newdev;



